import { createSlice } from "@reduxjs/toolkit";
import { registerThunk, loginThunk } from "./authThunks";
import AuthStatus from "../../constants/userRolesEnum";

const INITIAL_STATE = {
  token: null,
  user: {
    email: null,
    login: null,
  },
  isAuthenticated: false, //     
  authSwitchToShow: AuthStatus.LogIn,
  isLoading: false,
  error: null,
};

const authSlice = createSlice({
  // ' 
  name: "auth",
  //    
  initialState: INITIAL_STATE,
  reducers: {
    logOut(state) {
      state.token = null;
      state.user = {
        login: null,
        email: null,
      };
      state.isAuthenticated = false; //     
      state.authSwitchToShow = AuthStatus.LogIn; //       (signin  signup)
      state.error = null;
    },
    setAuthSwitchToShow(state, action) {
      state.authSwitchToShow = action.payload;
    },
    setToken(state, action) {
      state.token = action.token;
      state.isAuthenticated = true;
      state.authSwitchToShow = AuthStatus.LogOut;
    },
    setCurrentUserWithToken(state, action) {
      state.user = action.payload;
      state.isAuthenticated = true;
      state.authSwitchToShow = AuthStatus.LogOut;
      state.error = null;
    }
  },
  extraReducers: (builder) =>
    builder
      // ---------- REGISTER USER ----------------
      .addCase(registerThunk.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(registerThunk.fulfilled, (state, action) => {
        state.isLoading = false;
        state.authSwitchToShow = AuthStatus.LogIn; //    
        state.user = action.payload.user;
      })
      .addCase(registerThunk.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      })
      // ---------- LOGIN USER ----------------
      .addCase(loginThunk.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(loginThunk.fulfilled, (state, action) => {
        state.isLoading = false;
        state.isAuthenticated = true;
        state.authSwitchToShow = AuthStatus.LogOut;
        state.token = action.payload.token;
        state.user = action.payload.user;
        
      })
      .addCase(loginThunk.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;       
        
      })

  
  // // ---------- REFRESH USER ----------------
  // .addCase(refreshThunk.pending, (state) => {
  //   state.isLoading = true;
  //   state.error = null;
  // })
  // .addCase(refreshThunk.fulfilled, (state, action) => {
  //   state.isLoading = false;
  //   state.authenticated = true;
  //   state.user = action.payload;
  // })
  // .addCase(refreshThunk.rejected, (state, action) => {
  //   state.isLoading = false;
  //   state.error = action.payload;
  // }),
});

export const authReducer = authSlice.reducer;
export const { logOut, setAuthSwitchToShow, setToken, setCurrentUserWithToken } =
  authSlice.actions;
